import type { Meta, StoryFn } from '@storybook/web-components';
import { ButtonVariant } from '@tiger-analytics/ui';
import { html } from 'lit';

export default {
  title: 'Overlay/ta-modal',
  component: 'ta-modal',
  parameters: {
    controls: { expanded: true },
  },
  argTypes: {
    id: {
      description: 'Mandatory - unique id for testing',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    visible: {
      description: 'Controls the visibility of modal',
      table: {
        defaultValue: { summary: 'false' },
      },
      control: false,
    },
    title: {
      description: 'Title for the modal',
      table: {
        defaultValue: { summary: '' },
      },
      control: 'text',
    },
    onClose: {
      description:
        'Task to perform on close of the modal. Developers should use this to focus on the button which activated the modal for accessibility',
      table: {
        category: 'Custom Events',
      },
      control: false,
    },
  },
} as Meta;

const description = `The ta-modal component is used to display overlay dialogs for important content or actions, such as forms, notifications, or confirmations. It is ideal for scenarios where you need to focus the user's attention on a task while temporarily blocking interaction with the rest of the page. Use it when you want to present modal windows that require user interaction before proceeding.`;

const Template: StoryFn = (args) => {
  const toggleOverlayVisibility = () => {
    const overlay = document.querySelector('ta-modal') as HTMLElement;
    const isVisible = overlay?.hasAttribute('visible');
    if (isVisible) {
      overlay.removeAttribute('visible');
      const modalActivateButton = document.getElementById('activate-modal');
      if (modalActivateButton) {
        modalActivateButton.focus();
      }
    } else {
      overlay.setAttribute('visible', 'true');
    }
  };

  return html`
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column style="gap: 10px">
        <ta-button
          id="activate-modal"
          @click="${toggleOverlayVisibility}"
          variant="${ButtonVariant.Secondary}"
          >Show Overlay</ta-button
        >
        <ta-modal
          id="${args.id}"
          .visible="${args.visible}"
          .title="${args.title}"
          @onClose="${toggleOverlayVisibility}">
          <p>
            This is some content inside the overlay. Click the close button below to dismiss it.
          </p>
          <ta-button id="overlay-demo-button-1" @click="${() => window.alert('Dummy button')}"
            >Demo button 1</ta-button
          >
        </ta-modal>
        <ta-button
          id="overlay-demo-button-1"
          @click="${() => window.alert('Demo button 1')}"
          variant="${ButtonVariant.Destruct}"
          >Demo button 1</ta-button
        >
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      </ta-grid-column>
      <ta-grid-column>
        <div class="story-description">${description}</div>
      </ta-grid-column>
    </ta-grid-container>
  `;
};

export const CodeAndAccessibility = Template.bind({});
CodeAndAccessibility.args = {
  id: 'test-modal',
  visible: false,
  title: 'Modal title',
};
